.nsw-palette {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 0 0;
  grid-template-areas: ". . . . . . . . . . . ." ". . . . . . . . . . . ." ". . . . . . . . . . . ." ". . . . . . . . . . . .";
  font-size: var(--nsw-font-size-xxs-desktop);
  color: var(--nsw-text-light);
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}
.nsw-palette > div {
  padding: 0.5rem;
  white-space: normal;
  overflow-wrap: normal;
}

.nsw-theme-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  background: #f0f0f0;
}
@media (min-width: 62rem) {
  .nsw-theme-grid {
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 2rem;
  }
}
.nsw-theme-grid-container, .nsw-theme-grid-side-stack {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.25rem;
  width: 100%;
}
@media (min-width: 62rem) {
  .nsw-theme-grid-container, .nsw-theme-grid-side-stack {
    grid-auto-flow: column;
    grid-template-rows: repeat(4, auto);
    grid-auto-columns: 80px;
    gap: 0;
    /* overflow-x: auto; */
    width: auto;
    max-width: 100%;
  }
}
.nsw-theme-grid-swatch {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 10px;
  font-weight: 500;
  padding: 0.25rem;
  box-sizing: border-box;
  text-align: left;
  height: 80px;
}
@media (min-width: 62rem) {
  .nsw-theme-grid-swatch {
    height: 120px;
    width: 80px;
    font-size: 12px;
    padding: 0.5rem;
  }
}
.nsw-theme-grid-swatch span {
  line-height: 1.2;
}

.nsw-docs :not(pre) code {
  padding: 0.1875rem;
  background: var(--nsw-grey-04);
  border-radius: var(--nsw-border-radius);
}
@media (min-width: 62rem) {
  .nsw-docs :target::before {
    content: "";
    display: block;
    height: 9.4375rem;
    margin: -9.4375rem 0 0;
  }
}
.nsw-docs--light .nsw-docs__main {
  background-color: var(--nsw-white);
}
@media (min-width: 62rem) {
  .nsw-docs__header {
    height: 9.4375rem;
  }
}
@media (min-width: 62rem) {
  .nsw-docs__header-wrapper {
    z-index: 300;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: var(--nsw-white);
  }
}
.nsw-docs__header .nsw-container,
.nsw-docs__header .nsw-header__container {
  margin: 0;
}
@media (min-width: 62rem) {
  .nsw-docs__header .nsw-container,
  .nsw-docs__header .nsw-header__container {
    max-width: 77.5rem;
  }
  .nsw-docs__header .nsw-header__container .nsw-header__inner {
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 62rem) {
  .nsw-docs__wrapper {
    display: flex;
  }
}
.nsw-docs__nav {
  /* stylelint-disable max-nesting-depth */
  /* stylelint-enable max-nesting-depth */
}
@media (min-width: 62rem) {
  .nsw-docs__nav {
    min-width: 18.75rem;
    max-width: 18.75rem;
  }
}
@media (min-width: 62rem) {
  .nsw-docs__nav-wrapper {
    z-index: 300;
    position: fixed;
    top: 9.4375rem;
    bottom: 0;
    left: 0;
    width: 18.75rem;
    overflow-y: auto;
    border-right: 1px solid var(--nsw-grey-04);
    background-color: var(--nsw-white);
    padding: 1rem;
  }
}
.nsw-docs__nav .nsw-main-nav__sub-nav a .nsw-brand {
  margin-right: -0.5rem;
}
@media (min-width: 62rem) {
  .nsw-docs__nav .nsw-main-nav {
    background-color: transparent;
  }
  .nsw-docs__nav .nsw-main-nav li {
    border-top: 1px solid var(--nsw-grey-04);
  }
  .nsw-docs__nav .nsw-main-nav a {
    font-weight: var(--nsw-font-normal);
  }
  .nsw-docs__nav .nsw-main-nav a.active {
    font-weight: var(--nsw-font-bold);
  }
